import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';

import { DataService, Achievement } from '../server';

@Component({
  selector: 'app-achievements',
  templateUrl: './achievements.component.html',
  styleUrls: ['./achievements.component.scss']
})
export class AchievementsComponent implements OnInit {

  items: Achievement[];

  selected: Achievement = null;
  newItem: Achievement;
  private _selectedItemOriginalState = null;

  userId: number;

  constructor(private _data: DataService, private _route: ActivatedRoute) {
  }

  ngOnInit() {
    this._route.params
      .map((params: Params) => params['userId'] ? +params['userId'] : null)
      .do((userId: number) => this.userId = userId)
      .switchMap((userId: number) => this._data.getAchievements(userId))
      .subscribe((achievements: Achievement[]) => this.items = achievements);

    this.newItem = { id: null, distance: null, time: null, date: new Date() };
  }

  select(item: Achievement, e: any) {
    // if (this.selected !== null) {
    //   return;
    // }
    if (this._selectedItemOriginalState) {
      Object.assign(this.selected, this._selectedItemOriginalState);
    }
    if (item) {
      this._selectedItemOriginalState = Object.assign({}, item);
      this.selected = item;
    }
  }
  unselect() {
    this._selectedItemOriginalState = null;
    this.selected = null;
  }

  createItem(item, e) {
    e.preventDefault();
    e.stopImmediatePropagation();
    this._data.createAchievement(item).subscribe(
      update => {
           this.items.unshift(this.newItem);
        this.newItem = new Achievement();
      }
    );
  }

  updateItem(item: Achievement, e: any) {
    e.preventDefault();
    e.stopImmediatePropagation();
    this._data.updateAchievement(item).subscribe(
      update => {
        Object.assign(item, update);
        this.unselect();
      }
    );

  }

  deleteItem(item: Achievement, e: any) {
    e.preventDefault();
    e.stopImmediatePropagation();
    this._data.deleteAchievement(item.id).subscribe(
      update => {
        this.unselect();
        let index = this.items.indexOf(item);
        this.items.splice(index, 1);
      }
    );
  }

  discardChanges(item: Achievement, e: any) {
    e.preventDefault();
    e.stopImmediatePropagation();

    if (this._selectedItemOriginalState) {
      Object.assign(this.selected, this._selectedItemOriginalState);
    }
    this.unselect();
  }
}




