import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule, Routes } from '@angular/router';

import { AppComponent } from './app.component';

import { AccountService, DataService } from './server';
import { AchievementsComponent } from './achievements/achievements.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { TimePickerDirective } from './time-picker.directive';
import { UsersComponent } from './users/users.component';


const appRoutes: Routes = [
  ///{ path: 'crisis-center', component: CrisisListComponent },
  // { path: 'user/:id',      component: HeroDetailComponent },
  {
    path: 'achievements/:userId',
    component: AchievementsComponent,
  },
  {
    path: 'achievements',
    component: AchievementsComponent,
  },
  {
    path: 'people',
    component: UsersComponent,
  },
  {
    path: '',
    redirectTo: '/achievements',
    pathMatch: 'full'
  },
  { path: '**', component: PageNotFoundComponent }
];



@NgModule({
  declarations: [
    AppComponent,
    AchievementsComponent,
    PageNotFoundComponent,
    TimePickerDirective,
    UsersComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(appRoutes)

  ],
  providers: [AccountService, DataService],
  bootstrap: [AppComponent]
})
export class AppModule { }




