import { Injectable } from '@angular/core';
import { Observable } from '../rx';

@Injectable()
export class DataService {

  constructor() { }

  getUsers(): Observable<UserViewModel[]> {
    return Observable.from([
      [
        { id: 5, isManager: false, name: 'Alex Oldmen', managerName: 'Mark', managerId: 1, avgSpeed: 8.4, distance: 40.00, lastUpdate: new Date() },
        { id: 2, isManager: false, name: 'Daniel Bobyson', managerName: 'Mark', managerId: 1, avgSpeed: 11.6, distance: 74.65, lastUpdate: new Date() },
        { id: 1, isManager: true, name: 'Mark Vanduwer', managerName: null, managerId: null, avgSpeed: 7.60, distance: 23.00, lastUpdate: new Date() },
        { id: 4, isManager: false, name: 'Robert Milkevich', managerName: 'Mark', managerId: 1, avgSpeed: 5.32, distance: 42.00, lastUpdate: new Date() },
      ]
    ]);
  }

  deleteUser(userId: number): Observable<string> {
    console.log('user deleted:' + userId);
    return Observable.from(['']);
  }

  getAchievements(userId: number): Observable<Achievement[]> {
    return Observable.from([[
      { id: 1, distance: 55, time: '23:14', date: new Date() },
      { id: 2, distance: 55, time: '23:14', date: new Date() },
      { id: 3, distance: 55, time: '23:14', date: new Date() },
      { id: 4, distance: 55, time: '23:14', date: new Date() },
      { id: 5, distance: 55, time: '23:14', date: new Date() },
    ]]);
  }

  createAchievement(item: Achievement): Observable<Achievement> {
    item.id = Math.floor(Math.random() * 10000);
    return Observable.from([item]);
  }
  updateAchievement(item: Achievement): Observable<Achievement> {
    item.distance *= 2;
    return Observable.from([item]);
  }

  deleteAchievement(itemId: number): Observable<string> {
    return Observable.from(['OK']);
  }




}

export class UserViewModel {
  id: number;
  name: string;
  isManager: boolean;
  managerName: string;
  managerId: number;
  avgSpeed: number;
  distance: number;
  lastUpdate: Date;
}

export class Achievement {
  id: number;
  distance: number;
  time: string;
  date: Date;

  constructor() {
    this.date = new Date();
  }
}



